import {
  Modal,
  Form,
  Input,
  Upload,
  Avatar,
  Select,
  Typography,
  Spin,
} from 'antd';
import { MessageOutlined } from '@ant-design/icons';
import React, { useEffect } from 'react';
import {
  useAppDispatch,
  useAppSelector,
  useImageUpload,
  useErrorMessage,
  useSuccessMessage,
} from '../../app/hooks';
import {
  createChannel,
  creatorClosed,
  creationErrorChanged,
  createdSucceedChanged,
} from '../../features/channel/channelSlice';
import { Channel } from '../../app/types';
import { fetchUserContacts } from '../../features/user/userSlice';

const { Option } = Select;

export const CreateChannelForm: React.FC = () => {
  const isOpened = useAppSelector((state) => state.channel.isCreatorOpened);
  const isLoading = useAppSelector((state) => state.channel.isCreating);
  const succeed = useAppSelector((state) => state.channel.createdSucceed);
  const error = useAppSelector((state) => state.channel.creationError);
  const contacts = useAppSelector((state) => state.user.contacts);
  const totalContacts = useAppSelector((state) => state.user.contactsTotal);
  const isContactsFetching = useAppSelector(
    (state) => state.user.isContactsFetching
  );
  const contactsCount = useAppSelector((state) => state.user.contactsCount);
  const contactsPage = useAppSelector(
    (state) => state.user.contactsLastPortion
  );

  const { img, setImg, dummyRequest, beforeUpload, handleChange } =
    useImageUpload(null);
  const [form] = Form.useForm();

  const dispatch = useAppDispatch();

  useErrorMessage(error, creationErrorChanged);
  useSuccessMessage(' ', succeed, createdSucceedChanged);

  useEffect(() => {
    if (isOpened) {
      form.resetFields();
      setImg(null);
    }
  }, [form, isOpened, setImg]);

  const close = () => {
    dispatch(creatorClosed());
  };

  const okHandler = () => {
    form.submit();
  };

  const scrollHandler = (e: React.UIEvent) => {
    const target = e.currentTarget;
    const scrollBottom =
      target.scrollHeight - target.scrollTop - target.clientHeight;
    if (
      scrollBottom < 50 &&
      !isContactsFetching &&
      contactsCount * contactsPage < (totalContacts ?? 0)
    ) {
      dispatch(
        fetchUserContacts({
          count: contactsCount,
          portion: contactsPage + 1,
        })
      );
    }
  };

  const submitHandler = (formData: Channel) => {
    dispatch(createChannel({ ...formData, photo: img }));
  };

  return (
    <Modal
      title="  "
      centered
      visible={isOpened}
      onOk={okHandler}
      onCancel={close}
      okButtonProps={{ loading: isLoading }}
      cancelButtonProps={{ disabled: isLoading }}
      width={800}
    >
      <div className="flex gap-5">
        <Upload
          name="channel"
          showUploadList={false}
          beforeUpload={beforeUpload}
          customRequest={dummyRequest}
          onChange={handleChange}
        >
          <Avatar
            size={200}
            shape="square"
            src={img}
            icon={<MessageOutlined />}
          />
        </Upload>
        <Form
          form={form}
          name="createChannel"
          onFinish={submitHandler}
          className="flex-grow"
        >
          <Form.Item
            name="name"
            rules={[
              {
                required: true,
                message: ' ,   ',
                whitespace: true,
              },
            ]}
          >
            <Input placeholder=" " />
          </Form.Item>

          <Form.Item name="description" initialValue={null}>
            <Input placeholder="  ( ')" />
          </Form.Item>

          <Form.Item name="members" initialValue={[]}>
            <Select
              mode="multiple"
              style={{ width: '100%' }}
              placeholder="     "
              onPopupScroll={scrollHandler}
              dropdownRender={(menu) => (
                <div>
                  {menu}
                  {isContactsFetching && (
                    <div className="flex justify-center">
                      <Spin />
                    </div>
                  )}
                </div>
              )}
            >
              {contacts.map((c) => (
                <Option value={c.login} key={c.id}>
                  <Typography.Text>{c.login} </Typography.Text>
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Form>
      </div>
    </Modal>
  );
};
