import React, { useState } from 'react';
import { Form, Input, Select, Button } from 'antd';
import { Status, User, WithPassword } from '../app/types';
import { Link } from 'react-router-dom';
import { useRedirectFromAuth } from '../features/auth/hooks';
import { useAppDispatch, useAppSelector, useErrorMessage } from '../app/hooks';
import { signingUpErrorChanged, signUp } from '../features/auth/authSlice';

const { Option } = Select;

type FormValues = User & WithPassword;

export const SignUp: React.FC = () => {
  const isLoading = useAppSelector((state) => state.auth.isSigningUp);
  const error = useAppSelector((state) => state.auth.signingUpError);
  const dispatch = useAppDispatch();
  useErrorMessage(error, signingUpErrorChanged);
  const [status, setStatus] = useState<Status | null>(null);
  useRedirectFromAuth();

  const statusChangeHandler = (value: Status) => {
    setStatus(value);
  };

  const submitHandler = (values: FormValues) => {
    dispatch(signUp(values));
  };

  return (
    <div className="w-96 ml-auto mr-auto self-center">
      <Form name="registration" onFinish={submitHandler}>
        <Form.Item
          name="login"
          rules={[
            {
              required: true,
              message: ' ,  ',
              whitespace: true,
            },

            {
              min: 4,
              message: '      4 ',
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="password"
          rules={[
            {
              required: true,
              message: ' ,  ',
              whitespace: true,
            },
            {
              min: 6,
              message: '      6 ',
            },
          ]}
        >
          <Input.Password placeholder="" />
        </Form.Item>

        <Form.Item
          name="name"
          rules={[
            {
              required: true,
              message: " ,   '",
              whitespace: true,
            },
          ]}
        >
          <Input placeholder="..." />
        </Form.Item>

        <Form.Item
          name="status"
          rules={[
            { required: true, message: ' ,   ' },
          ]}
        >
          <Select placeholder=" " onChange={statusChangeHandler}>
            <Option value="student"></Option>
            <Option value="instructor"></Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="department"
          rules={[
            {
              required: true,
              message: ' ,    ',
              whitespace: true,
            },
          ]}
        >
          <Input placeholder=" " />
        </Form.Item>

        {status === 'student' && (
          <Form.Item
            name="group"
            rules={[
              {
                required: true,
                message: ' ,    ',
                whitespace: true,
              },
            ]}
          >
            <Input placeholder=" " />
          </Form.Item>
        )}

        <Form.Item>
          <Button type="primary" htmlType="submit" loading={isLoading}>
            
          </Button>
          <Link to="/sign-in">
            <div className="inline-block text-blue-500 py-1 px-3 hover:text-blue-400 focus:text-blue-400">
              
            </div>
          </Link>
        </Form.Item>
      </Form>
    </div>
  );
};
